// articleService.jsx
import axios from 'axios';

const API_URL = 'http://localhost:3001/api/articles';

const handleResponse = (response) => {
  if (response.status >= 200 && response.status < 300) {
    return response.data;
  } else {
    throw new Error(`Error en la solicitud: ${response.status}`);
  }
};

const createArticle = async (articleData) => {
  try {
    console.log("articleService - createArticle: Datos enviados al backend:", articleData);
    const response = await axios.post(API_URL, articleData, {
      headers: { 'Content-Type': 'multipart/form-data' },
    });
    return handleResponse(response);
  } catch (error) {
    console.error("Error al crear el artículo:", error.message);
    throw error;
  }
};

const articleService = {
  createArticle,
};

export default articleService;
