import React, { useState } from "react";
import Modal from "../../molecules/Modal/Modal";
import Form from "../../molecules/Form/Form";
import articleService from "../../../services/articleService";
import 'bootstrap/dist/css/bootstrap.min.css';

const SubmitArticle = ({ fetchData, handleClose }) => {
  const [titulo, setTitulo] = useState("");
  const [contenido, setContenido] = useState("");
  const [autor, setAutor] = useState("");
  const [pdf, setPdf] = useState(null);
  const [foto, setFoto] = useState(null);

  const handleSubmit = async (e) => {
    e.preventDefault();
    console.log("SubmitArticle - handleSubmit: Datos capturados", { titulo, contenido, autor, pdf, foto });

    const formData = new FormData();
    formData.append('titulo', titulo);
    formData.append('contenido', contenido);
    formData.append('autor', autor);
    if (pdf) formData.append('pdf', pdf);
    if (foto) formData.append('foto', foto);

    try {
      await articleService.createArticle(formData);
      fetchData();
      setTitulo('');
      setContenido('');
      setAutor('');
      setPdf(null);
      setFoto(null);
      alert('Artículo enviado exitosamente');
      handleClose();
    } catch (error) {
      console.error('Error al crear el artículo:', error);
      alert('Error al enviar el artículo.');
    }
  };

  const fields = [
    { name: "titulo", label: "Título", type: "text", value: titulo, onChange: (e) => setTitulo(e.target.value) },
    { name: "contenido", label: "Contenido", type: "textarea", value: contenido, onChange: (e) => setContenido(e.target.value) },
    { name: "autor", label: "Autor", type: "text", value: autor, onChange: (e) => setAutor(e.target.value) },
    { name: "pdf", label: "Archivo PDF", type: "file", onChange: (e) => setPdf(e.target.files[0]) },
    { name: "foto", label: "Foto", type: "file", onChange: (e) => setFoto(e.target.files[0]) }
  ];

  return (
    <Modal show={true} handleClose={handleClose}>
      <h2 className="text-center mb-3">Enviar Artículo</h2>
      <Form fields={fields} onSubmit={handleSubmit} buttonLabel="Enviar Artículo" />
    </Modal>
  );
};

export default SubmitArticle;
